import {
  GET_ALL_CLIENTS,
  LOGIN,
  POST_NEW_CLIENT,
  UPDATE_CLIENT,
  DELETE_CLIENT,
} from "./types";

export const getAllClients = () => (dispatch) => {
  fetch("/clients/all")
    .then((res) => res.json())
    .then((clients) =>
      dispatch({
        type: GET_ALL_CLIENTS,
        payload: clients,
      })
    )
    .catch((err) => console.log("redux clients error: ", err));
};

export const login = (mail, password) => (dispatch) => {
  const body = JSON.stringify({ mail, password });
  fetch("/clients/login", {
    method: "POST",
    headers: {
      "Content-type": "application/json",
    },
    body,
  })
    .then((res) => {
      if (res.status === 400) {
        return alert("invalid credentials");
      } else {
        return res.json();
      }
    })
    .then((data) => {
      console.log("action data: ", data);
      dispatch({
        type: LOGIN,
        isAuth: true,
      });
      localStorage.setItem("user", JSON.stringify(data));
    })
    .catch((err) => console.log("redux login error: ", err));
};
